.header {
  background-color: var(--accent-color);
  position: relative;

  &__wrapper {
    display: flex;
    align-items: center;
    gap: 10%;
  }
  &__main {
    flex-grow: 1;
    padding: 30px 0px;
  }

  &__main-top {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-bottom: 24px;
  }

  &__phone {
    display: flex;
    align-items: center;
    margin-left: 198px;
  }

  &__phone-link {
    margin-left: 12px;
    font-family: "Alegreya Sans";
    font-size: 24px;
    line-height: 130%;
  }
  &__phone:hover{
    color: var(--btn-color);
  }

  &__burger {
    display: none;
  }

  &__nav-mobile {
    position: absolute;
    right: -500px;
    top: 0;
    background: var(--accent-color);
    padding: 42px 43px 40px;
    border-radius: 13px;
    transition: right 0.5s ease;
    z-index: 1000;
    .nav__list {
      display: block;
      position: relative;
    }
    .nav__item {
      margin-bottom: 30px;
    }
    .nav__link {
      font-family: "Reggae One";
      font-size: 16px;
      line-height: 140%; /* 22.4px */
      letter-spacing: -0.8px;
      text-transform: uppercase;
    }
    .social {
      flex-direction: row;
      margin-top: 40px;
    }
    &--close {
      position: absolute;
      top: 16px;
      right: 17px;
      cursor: pointer;
    }
  }

}

.header__nav-mobile.active {
  right: 0;
}
.header__nav-mobile--wrapper.active {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 1000;
  transition: all 0.5s ease;
}


.social {
  display: flex;
  flex-direction: column;
  gap: 6px;
  flex-shrink: 0;
}
.nav {

  &__list {
    display: flex;
    gap: 23px;
    justify-content: flex-end;

  }
  &__item {
    transition: border-bottom 0.3s ease;
  }

  &__link {
    text-transform: uppercase;
    text-align: center;
  }


  .header__nav-item:hover {
    position: relative;
    &::after {
      content: "";
      position: absolute;
      bottom: -2px;
      left: 0;
      display: block;
      width: 100%;
      height: 1px;
      background: var(--btn-color);
    }

  }


  // &__link:hover {
  //   border-bottom: 1px solid var(--btn-color);
  // }
}

@media (max-width: 1010px) {
  .header__social{
    display: none;
  }
  .header__nav {
    display: none;
  }
  .header__nav-mobile{
    display: block;
  }
  .header__phone {
    order: 1;
    margin-left: 0;
  }
  .header__logo {
    order: 2;
    margin-left: -144px;
  }
  .header__logo img{
    width: 160px;
    height: 40px;
  }
  .header__main-top {
    justify-content: space-between;
    margin-bottom: 0;
  }

  .header__burger{
    display: block;
    order: 3;
    cursor: pointer;
  }

  .header__main {
    padding: 17px 0px;
  }

}

@media (max-width: 600px) {
  .header__phone-link {
    display: none;
  }
  .header__logo {
    margin-left: 0px;
  }
  .header__logo img {
    width: 112px;
    height: 28px;
  }

}




