.franchise {
  padding-top: 50px;

  &__main-bg {
    border-radius: 17px;
    margin-bottom: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    background: url("../img/franchise-main.jpg") no-repeat center;
    background-size: cover;
    min-height: 280px;
  }

  &__main-title {
    text-align: center;
    font-size: 70px;
    line-height: 120%; /* 84px */
    letter-spacing: -7px;
  }

  &__content {
    padding: 50px 0;
  }

  &__content-wrapper {
    position: relative;
    max-width: 1090px;
    margin: 0 auto;
  }

  &__content-title {
    text-align: center;
    font-size: 40px;
    line-height: 110%; /* 44px */
    letter-spacing: -4px;
    margin-bottom: 30px;
  }

  &__content-text {

    text-align: justify;
/* T2 */
    font-family: "Alegreya Sans";
    font-size: 24px;
    line-height: 130%; /* 31.2px */

  }

  &__about {
    padding: 50px 0;
    text-align: center;
    padding-bottom: 79px;
  }

  &__about-title {
    text-align: center;
    font-size: 40px;
    line-height: 110%; /* 44px */
    letter-spacing: -4px;
    margin-bottom: 40px;
  }

  &__about-content {
    font-family: "Alegreya Sans";
    font-size: 24px;
    line-height: 130%;
    margin-bottom: 10px;
  }

  &__logo {
    position: absolute;
    bottom: -12px;
    left: -44px;
  }

  &__about-tel {
    margin: 10px 0;
  }

  &__about-email {
    margin-bottom: 20px;
  }

}

@media (max-width: 1010px) {
  .franchise__main-bg {
    min-height: 180px;
    margin-bottom: 25px;
  }
  .franchise__main-title {
    font-size: 40px;
    line-height: 110%; /* 44px */
    letter-spacing: -4px;
    background: url("../img/blur-franchise.png") no-repeat center;
    background-size: contain;
    padding: 20px;
  }
  .franchise__content-title {
    font-size: 20px;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    text-transform: uppercase;
    margin-bottom: 20px;
  }
  .franchise__content {
    padding: 25px 0;
  }
  .franchise__content-wrapper {
    max-width: 100%;
    margin: auto;
  }
  .franchise__content-text {
    font-size: 16px;
    line-height: 130%;
  }
  .franchise__about {
    padding: 25px 0;
    padding-bottom: 37px;
  }
  .franchise__about-title {
    font-size: 20px;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    text-transform: uppercase;
    margin: auto;
    max-width: 548px;
    text-align: center;
    margin-bottom: 24px;
  }
  .franchise__logo {
    bottom: -2px;
    left: 24px;
  }

  .franchise__about-content {
    font-family: "Alegreya Sans";
    font-size: 20px;
    line-height: 100%;
  }

  .franchise__about-tel {
    margin: 16px 0;
  }

  .franchise__about-email {
    margin-bottom: 20px;
  }
  .franchise__logo img {
    width: 150px;
    height: 147px;
  }
}

@media (max-width: 600px) {
  .franchise {
    padding-top: 39px;
  }

  .franchise__main-bg {
    min-height: 80px;
    margin-bottom: 16px;
  }
  .franchise__main-title {
    font-size: 20px;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    text-transform: uppercase;
    padding: 0px;
  }
  .franchise__content-title {
    font-size: 16px;
    line-height: 140%; /* 22.4px */
    letter-spacing: -0.8px;
    text-transform: uppercase;
    margin-bottom: 12px;
  }
  .franchise__content {
    padding: 25px 0;
  }
  .franchise__content-wrapper {
    max-width: 100%;
    margin: auto;
  }
  .franchise__content-text {
    font-size: 14px;
    font-weight: 300;
    line-height: 110%;
  }
  .franchise__about {
    padding: 9px 0;
    padding-bottom: 4px;
  }
  .franchise__about-title {
    font-size: 15.7px;
    line-height: 140%; /* 22.4px */
    letter-spacing: -0.8px;
    text-transform: uppercase;
    max-width: 100%;
    margin-bottom: 10px;
  }
  .franchise__logo {
    bottom: -10px;
    left: auto;
    right: -10px;
  }

  .franchise__about-content {
    font-size: 16px;
    line-height: 130%;
  }

  .franchise__about-tel {
    margin: 11px 0;
  }

  .franchise__about-email {
    margin-bottom: 13px;
  }
  .franchise__logo img {
    width: 72px;
    height: 109px;
    transform: scale(-1, 1)
  }
  .franchise__about-qr img{
    width: 72px;
    height: 72px;
  }
}





