.menu {
  margin: 30px 0;
  margin-bottom: 41px;
  &__container {
    padding-left: 0px;
    padding-right: 0px;
    position: relative;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    gap: 30px;
  }

  &__navigation-bg {
    position: sticky;
    top: 0;
    border-radius: 15px;
    background: var(--accent-color);
    min-height: 1320px;
    padding: 31px 61px 31px 64px;

  }

  &__item-wrapper {
    display: none;
    &.active {
      display: block;
    }
  }

  &__item {
    position: relative;
  }

  &__item-navigation {
    top: 0;
    height: 100%;
    width: 370px;
    flex-shrink: 0;
  }

  &__navigation-nav {
  }

  &__navigation-title {
    margin-bottom: 43px;
    font-size: 40px;
    line-height: 110%; /* 44px */
    letter-spacing: -4px;
  }

  &__navigation-list {
    display: flex;
    flex-direction: column;
  }

  &__navigation-item {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid var(--body-color);
    font-size: 20px;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    text-transform: uppercase;
    cursor: pointer;
    &.active{
      color: var(--accent-text-color);
    }
  }
  &__logo-img {
    position: absolute;
    bottom: -54px;
    left: 76px;
  }
}

.menu {
  &__item-cards {
    width: 722px;
  }

  &__cards-wrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
  }

  &__cards-item {
    position: relative;
    flex-shrink: 0;
    background: #fff;
    width: 346px;
    height: 504px;
    border-radius: 15px;
    color: var(--text-color-dark);
    picture img {
      height: 290px;
      border-radius: 15px;
    }
  }

  &__cards-content {
    padding: 0px 20px 20px 20px;
    margin-top: -3px;
    min-height: 215px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__cards-title {
    text-align: center;
    font-size: 16px;
    line-height: 140%; /* 22.4px */
    letter-spacing: -0.8px;
    text-transform: uppercase;
  }

  &__cards-text {
    margin-bottom: 12px;
    font-family: "Alegreya Sans";
    font-size: 16px;
    line-height: 130%; /* 20.8px */
  }

  &__cards-icons {
    display: flex;
    gap: 4px;
    margin-bottom: 20px;
  }

  &__cards-icon {
    flex-shrink: 0;
    border-radius: 50%;
    border: 2px solid var(--btn-color);
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    &.active {
      background-color: var(--btn-color);
    }
  }
  &__cards-hit {
    position: absolute;
    left: 12px;
    top: 0px;
  }
  &__cards-new {
    position: absolute;
    right: 12px;
    top: 13px;
  }

  &__cards-order {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
  }

  &__cards-price {
    font-size: 16px;
    line-height: 140%;
    letter-spacing: -0.8px;
    text-transform: uppercase;
    span {
      font-size: 30px;
      line-height: 110%; /* 33px */
      letter-spacing: -3px;
    }
  }

  &__cards-btn {
    font-family: "Alegreya Sans";
    font-size: 16px;
    font-weight: 700;
    line-height: 130%; /* 20.8px */
    color: var(--text-color);
    text-transform: uppercase;
    border-radius: 10px;
    background-color: var(--btn-color);
    width: 172px;
    padding: 10px 0;
  }
}

.menu {
  &__item-offer {
    width: 288px;
    flex-shrink: 0;
  }

  &__offer-wrapper {
    min-height: 1320px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    justify-content: space-between;
  }

  &__offer-item {
    position: relative;
    width: 288px;
    height: 420px;
    display: flex;
    justify-content: center;
    align-items: end;
    border-radius: 15px;
  }

  &__offer-bg {
    img {
      position: absolute;
      top: 0px;
      right: 0px;
    }
    &:first-child {
      background: url('../img/menu-bg-1.png') no-repeat center;
      background-size: cover;
    }
    &:nth-child(2) {
      background: url('../img/menu-bg-2.png') no-repeat center;
      background-size: cover;
    }
    &:last-child {
      background: url('../img/menu-bg-3.png') no-repeat center;
      background-size: cover;
    }
  }

  &__offer-title {
    text-align: center;
    font-size: 40px;
    line-height: 110%; /* 44px */
    letter-spacing: -4px;
    padding: 30px;
    &--bg-1 {
      background: url('../img/offer-blur-1.png') no-repeat center;
      background-size: contain;
    }
    &--bg-2 {
      font-size: 30px;
      background: url('../img/offer-blur-2.png') no-repeat center;
      background-size: contain;
      margin-bottom: 44px;
    }
    &--bg-3 {
      font-size: 30px;
      background: url('../img/offer-blur-3.png') no-repeat center;
      background-size: contain;
      margin-bottom: 44px;
    }
  }

  &__offer-text {
    background: url('../img/offer-blur-text.png') no-repeat center;
    background-size: contain;
    font-family: "Alegreya Sans";
    font-size: 20px;
    line-height: 120%; /* 24px */
    margin-top: 21px;
    text-align: center;
    padding: 10px;
    margin-bottom: 55px;
  }
}

// плавное появление карточек
.element-animation{
  top: 0px;
  opacity: 0;
  position: relative;
}
.element-animation.element-show{
  position: relative;
  // top: -20px;
  opacity: 1;
  transition-duration: 1.2s;
}

.menu {

  &__modal {
    display: none;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    max-width: 623px;
    height: 338px;
    border-radius: 15px;
    background: var(--accent-color);
    padding: 60px 102px;
    text-align: center;
    transition: display 0.8s;
    &.active{
      display: block;
    }
  }

  &__modal-title {

    font-size: 16px;
    line-height: 140%; /* 22.4px */
    letter-spacing: -0.8px;
    text-transform: uppercase;
    margin-bottom: 20px;
  }

  &__modal-hr {
    width: 100%;
    height: 2px;
    border-radius: 15px;
    background-color: var(--btn-color);
    margin-bottom: 30px;
  }

  &__modal-phone {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    margin-bottom: 40px;
  }

  &__modal-link {
    font-family: "Alegreya Sans";
    font-size: 30px;
    line-height: 130%; /* 39px */
  }

  &__modal-btn {
    width: 100%;
    border-radius: 10px;
    background-color: var(--btn-color);
    font-family: "Alegreya Sans";
    font-size: 20px;
    font-weight: 700;
    line-height: 130%; /* 26px */
    text-transform: uppercase;
    height: 60px;
    color: #fff;
  }
  &__offer-icon--tablet {
    display: none;
  }
}

@media (max-width: 1439px) {
  .menu__wrapper {
    justify-content: center;
  }
  .menu__item-cards {
    width: 722px;
    width: min-content;
  }
}


@media (max-width: 1100px) {
  .menu__wrapper {
    gap: 20px;
  }
}

@media (max-width: 1045px) {
  .menu__wrapper {
    display: block;
  }

  .menu__container {
    padding-left: 23px;
    padding-right: 23px;
    position: relative;
  }

  .menu__item-navigation {
    width: 100%;
    height: auto;
  }

  .menu__navigation-bg {
    position: relative;
    top: 0;
    min-height: auto;
    padding: 21px 14px;
  }
  .menu__navigation-title {
    text-align: center;
    font-size: 20px;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    text-transform: uppercase;
    padding-bottom: 5px;
    margin-bottom: 10px;
    border-bottom: 1px solid var(--text-color-dark);
  }

  .menu__navigation-list {
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    gap: 54px;
  }
  .menu__navigation-item{
    font-size: 16px;
    line-height: 140%; /* 22.4px */
    letter-spacing: -0.8px;
    text-transform: uppercase;
    padding-bottom: 0;
    margin-bottom: 0;
    border: none;
  }

  .menu__item-navigation {
    margin-bottom: 22px;
  }

  .menu__item-cards {
    margin: auto;
    margin-bottom: 20px;
  }

  .menu__item-offer {
    width: 100%;
  }

  .menu__offer-wrapper {
    min-height: auto;
    flex-direction: row;
    gap: 16px;
    justify-content: center;
  }

  .menu__offer-item img{
    width: 230px;
    height: 230px;
  }
  .menu__offer-item{
    width: 230px;
    height: 230px;
  }
  .menu__offer-title {
    font-size: 18px;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    padding: 7px;
    text-transform: uppercase;
  }
  .menu__offer-text {
    font-size: 16px;
    line-height: 130%;
    margin-top: -5px;
    margin-bottom: -2px;
    padding: 10px;

  }

  .menu__offer-item img{
    width: 80px;
    height: 80px;
  }
  .menu__offer-bg img {
    position: absolute;
    top: 0px;
    right: auto;
    left: 0;
  }
  .menu__logo-img {
    display: none;
  }

  .menu__offer-icon {
    display: none;
  }

  .menu__offer-icon--tablet {
    display: block;
  }

  .menu__offer-title--bg-2 {
    font-size: 20px;
    margin-bottom: 12px;
  }
  .menu__offer-title--bg-3 {
    font-size: 20px;
    margin-bottom: 25px;
  }

  .menu {
    margin-bottom: 50px;
  }

  .menu__item-cards {
    width: 100%;
  }

  .menu__modal {
    padding: 40px;
  }
}

@media (max-width: 762px) {
  .menu__navigation-list {
    gap: 25px;
  }

  .menu__modal {
    width: 300px;
    height: 250px;
  }

  .menu__modal-title {
    font-size: 12px;
    line-height: 120%; /* 14.4px */
    letter-spacing: -0.6px;
    text-transform: uppercase;
    margin-bottom: 12px;
  }
  .menu__modal-hr {
    margin-bottom: 20px;
  }
  .menu__modal-link {
    font-family: "Alegreya Sans";
    font-size: 20px;
    line-height: 120%;
  }
  .menu__modal-btn {
    height: 42px;
    border-radius: 101px;
  }
  .menu__modal-phone {
    margin-bottom: 30px;
  }
}

@media (max-width: 620px) {
  .menu__offer-wrapper {
    flex-direction: column;
    gap: 30px;
  }
  .menu__offer-item {
    width: 100%;
  }
  .menu__offer-title {
    font-size: 30px;
    padding: 30px;
  }
  .menu__offer-text {
    font-size: 20px;
    background: none;
  }
}
@media (max-width: 500px) {
  .menu__navigation-list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .menu__navigation-title {
    font-size: 14px;
  }
  .menu__navigation-item {
    font-size: 12px;
  }
  .menu__navigation-list {
    gap: 5px;
  }

  .menu__cards-item {
    width: 300px;
    height: 423px;
  }
  .menu__container {
    padding-left: 10px;
    padding-right: 10px;
  }
  .menu {
    margin: 40px 0;
  }
  .menu__navigation-bg {
    padding: 9px 10px;
    padding-bottom: 13px;
  }
  .menu__navigation-title {
    padding-bottom: 3px;
  }
  .menu__cards-hit {
    left: 1px;
  }
  .menu__cards-item picture img {
    height: 215px;
  }
  .menu__cards-content {
    padding: 0px 5px 18px 4px;
  }
  .menu__cards-title {
    margin-bottom: 7px;
  }
  .menu__cards-text {
    font-size: 14px;
    font-weight: 300;
    line-height: 110%
  }
  .menu__cards-text {
    margin-bottom: 18px;
  }
  .menu__cards-btn {
    width: 172px;
    height: 32px;
    padding: 0;
  }
  .menu__cards-price {
    font-size: 12px;
    line-height: 120%;
    letter-spacing: -0.6px;
    text-transform: uppercase;
  }
  .menu__cards-price span {
    font-size: 20px;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    text-transform: uppercase;
  }
  .menu__cards-content {
    min-height: 194px;
  }
  .menu__cards-wrapper {
    gap: 11px;
  }
  .menu__item-cards {
    margin-bottom: 44px;
  }

  .menu__offer-item {
    height: 160px;
  }
  .menu__offer-wrapper {
    justify-content: start;
  }

  .menu__offer-title {
    text-align: left;
    font-size: 16px;
    line-height: 140%; /* 22.4px */
    letter-spacing: -0.8px;
    text-transform: uppercase;
    padding: 30px;
    padding-left: 10px;
    margin-bottom: 0;
    width: fit-content;
  }
  .menu__offer-text {
    margin-top: -29px;
    text-align: left;
    font-size: 14px;
    font-weight: 300;
    line-height: 110%;
  }
  .menu__offer-wrapper {
    gap: 10px;
  }
  .menu__offer-title--bg-2 {
    max-width: 130px;
  }
  .menu__offer-item{
    justify-content: start;
  }
  .menu {
    margin-bottom: 60px;
  }
}





