.social {

  &__fix-wrapper {
    position: fixed;
    right: 64px;
    bottom: 64px;
    z-index: 800;
  }

  &__fix {
    display: flex;
    flex-direction: column;
  }

  &__fix-link {
    position: relative;
    transition: top 0.5s ease;
    &:nth-child(2){
      top: -95px;
      margin: 10px 0;
    }
    &:last-child{
      top: -190px;
    }
  }
}
.social__fix-wrapper:hover .social__fix-link{
  top: 0;
}

@media (max-width: 1010px) {
  .social__fix-link img{
    width: 50px;
    height: 50px;
  }
  .social__fix-link:nth-child(2){
    top: -50px;
    margin: 10px 0;
  }
  .social__fix-link:last-child{
    top: -100px;
  }

  .social__fix-wrapper {
    right: 10px;
  }
}

@media (max-width: 600px) {
  .social__fix-wrapper {
    display: none;
  }
}



