.footer {

  background: #2B2D2F;
  padding: 50px 0;
  padding-bottom: 76px;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    gap: 30px;

  }

  &__logo {
    margin-bottom: 35px;
  }
  &__logo-main {
    display: none;
  }

  &__contact-address {
    margin-bottom: 7px;
    display: flex;
    align-items: center;
    & img {
      margin-right: 12px;
    }
    & address {
      font-family: "Alegreya Sans";
      font-size: 24px;
      line-height: 130%;
    }

  }

  &__contact-phone {
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    & img {
      margin-right: 12px;
    }
    & a {
      font-family: "Alegreya Sans";
      font-size: 24px;
      line-height: 130%;
    }
  }

  &__social {
    flex-direction: row;
  }

  &__item-nav {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__item-social {
    padding-bottom: 19px;
  }

  &__social {
    gap: 10px;
  }

  &__item--about {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__item-inf {
    font-family: "Alegreya Sans";
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%; /* 24px */
  }

  &__item-developer {
    font-family: "Alegreya Sans";
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;
    text-align: right;
    padding-bottom: 20px;

  }
  &__item-conditions {
    margin-top: 43px;
    font-family: "Alegreya Sans";
    font-size: 24px;
    line-height: 120%;
  }

  &__nav {
    flex-direction: column;
    gap: 0;
    .nav__link {
      text-align: left;
    }
    .nav__item {
      margin-bottom: 30px;
    }
  }

  .footer__item--about{
    width: 283px;
  }
  .footer__item-contact{
    flex-basis: 527px;
  }
  .footer__item-nav{
    flex-basis: 273px;
  }

  &__item-social--tablet {
    display: none;
  }
  &__item-developer--tablet {
    display: none;
  }
  &__item-conditions--tablet {
    display: none;
  }
}

@media (max-width: 1010px) {
  .footer__logo {
    display: none;
  }
  .footer__logo-main{
    display: block;
    margin-bottom: 30px;
  }
  .footer__item-social--tablet {
    order: 4;
  }
  .footer__item-nav {
    order: 1;
  }
  .footer__item-contact {
    order: 2;
  }
  .footer__item--about {
    order: 3;
  }
  .footer__wrapper {
    flex-wrap: wrap;
  }

  .footer__item-social {
    display: none;
  }
  .footer__item-social--tablet {
    display: flex;
    align-items: end;
  }
  .footer__item-developer {
    display: none;
  }
  .footer__item-developer--tablet {
    display: block;
  }

  .footer__nav .nav__item {
    margin-bottom: 20px;
  }

  .footer__contact-address {
    margin-bottom: 6px;
  }

  .footer__contact-address address {
    font-size: 16px;
    line-height: 130%;
  }

  .footer__contact-phone a {
    font-size: 16px;
    line-height: 130%;
  }

  .footer__item:first-child {
    flex-basis: 363px;
  }

  .footer__item--about {
    flex-basis: 360px;
    margin-top: -70px;
  }

  .footer__item-inf {
    font-size: 20px;
    line-height: 120%;
  }

  .footer__item-conditions {
    display: none;
  }

  .footer__item-conditions--tablet {
    display: block;
    font-family: "Alegreya Sans";
    font-size: 24px;
    line-height: 130%;
    margin-bottom: 34px;
  }
  .footer__item-developer--tablet {
    text-align: center;
    margin-top: 40px;
    font-size: 14px;
    font-weight: 300;
    line-height: 110%;
  }

  .footer__item-social {
    padding-bottom: 8px;
  }
}

@media (max-width: 685px) {
  .footer__item--about {
    margin-top: 30px;
  }
}

@media (max-width: 600px) {
  .footer__logo-main img {
    width: 160px;
    height: 40px;
  }
  .footer__item-nav {
    margin-bottom: 10px;
  }
  .footer__item-social--tablet {
    margin: 0 auto;
    order: 2;
    margin-top: -32px;

  }
  .footer__item--about {
    margin-top: -12px;
  }
  .header__main {
    padding: 7px 0px;
  }
  .header__logo {
    margin-left: 20px;
  }
  .footer {
    background: #2B2D2F;
    padding: 40px 0;
    padding-bottom: 16px;
  }
  .footer__item:first-child {
    margin-top: -29px;
  }
}

