// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Reggae One", sans-serif;
  --content-width: 1312px;
  --container-offset: 64px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --body-color: #0D0100;
  --text-color: #fff;
  --text-color-dark: #0D0100;
  --btn-color: #EC7C26;
  --accent-text-color: #EC7C26;
  --accent-color: #2B2D2F;
}
