.dishes {

  &__wrapper {
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1.88rem;
    height: 26.25rem;
    margin-bottom: 6.25rem;
  }
}

.dish {

  &__item {
    position: relative;
    border-radius: 0.9375rem;
  }

  &__item-big {
    flex-basis: 54.0625rem;
    height: 100%;
  }

  &__item--duck {
    display: flex;
    align-items: center;
    background: url("../img/dishes-1.jpg") no-repeat center center;
    background-size: cover;
    padding: 0 3.13rem;
    & .dish__item-title {
      display: inline-block;
      padding-bottom: 3px;
      border-bottom: 1px solid var(--text-color);
    }
  }


  &__item-hit {
    position: absolute;
    top: 0rem;
    left: 0rem;
  }

  &__item-title {
    font-size: 2.5rem;
    line-height: 110%; /* 2.75rem */
    letter-spacing: -0.25rem;
    margin-bottom: 1.25rem;

  }
  &__item-title--bg{
    background: url("../img/blur-1.png") no-repeat center;
    background-size: contain;
  }

  &__item-title--bg-duck {
    background: url("../img/blur-2.png") no-repeat center;
    background-size: contain;
  }

  &__item-title--bg-dessert {
    background: url("../img/blur-3.png") no-repeat center;
    background-size: contain;
    padding: 1rem;
  }

  &__item-text {
    max-width: 17.75rem;
    font-family: "Alegreya Sans";
    font-size: 1.5rem;
    line-height: 130%;
  }

  &__item-small {
    flex-basis: 26.0625rem;
    height: 100%;
    display: flex;
    align-items: flex-end;
    text-align: center;
  }

  &__item--new {
    background: url("../img/dishes-2.jpg") no-repeat center center;
    background-size: cover;
  }

  &__item--dessert {
    background: url("../img/dishes-3.jpg") no-repeat center center;
    background-size: cover;
    justify-content: center;
  }

  &__item--sushi {
    background: url("../img/dishes-4.jpg") no-repeat center center;
    background-size: cover;
    display: flex;
    justify-content: flex-end;
    padding: 1.87rem 7.69rem;
  }
}

@media (max-width: 1010px) {
  .dishes__item {
    gap: 12px;
    height: 248px;
    margin-bottom: 50px;
  }

  .dish__item-big {
    flex-basis: 490px;
  }
  .dish__item-small {
    flex-basis: 246px;
  }
  .dish__item-hit img {
    width: 80px;
    height: 80px;
  }
  .dish__item-hit {
    top: 12px;
    left: 11px;
  }
  .dish__item-title {
    font-size: 20px;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    margin-bottom: 12px;
    text-transform: uppercase;
  }
  .dish__item-text {
    font-size: 20px;
    line-height: 120%; /* 24px */
    max-width: 300px;
  }

  .dish__item--sushi {
    padding: 21px 25px;
  }

  .dish__item--duck {
    padding: 0 19px;
  }
  .dish__item-content--duck {
    margin-top: 35px;
  }

  .dish__item-title--bg {
    margin-bottom: 16px;
  }
  .dish__item-title--bg-dessert{
    margin-bottom: 3px;
  }
}

@media (max-width: 600px) {
  .dishes__item {
    flex-wrap: wrap;
    margin-bottom: 40px;
  }
  .dish__item-big {
    flex-basis: 100%;
    height: 150px;
  }
  .dish__item-small {
    flex-basis: 100%;
    height: 240px;
  }

  .dishes__item {
    height: auto;
  }

  .dish__item-hit img {
    width: 48px;
    height: 48px;
  }

  .dish__item-text {
    font-size: 16px;
    line-height: 130%;
    max-width: 100%;
  }
  .dish__item--duck {
    padding: 0 8px;
  }
  .dish__item-content--duck {
    margin-top: 45px;
  }
  .dish__item--sushi {
    padding: 15px 19px;
  }

}
