.map {

  &__wrapper {
    display: flex;
    align-items: center;
    gap: 30px;
    justify-content: space-between;
    margin: 100px 0 92px;

  }
  &__wrapper-img {
    background: url("../img/contact-map.png") no-repeat center;
    background-size: cover;
    height: 470px;
    width: 865px;
    border-radius: 15px;
  }
  &__slider {
    width: 417px;
  }

  &__slider-item {
    position: relative;
  }

  &__item-bg {
    display: flex;
    align-items: flex-end;
    min-height: 470px;
    width: 417px;
    background: url('../img/map-people.png') no-repeat center;
    background-size: cover;
  }

  &__item-content {
    position: relative;
    padding: 15px 30px 58px;
    border-radius: 15px;
    background: url('../img/map-blur.png') no-repeat center;
    background-size: cover;
    width: 100%;
    margin-bottom: -10px;

  }

  &__item-content-text {

    display: flex;
    align-items: center;
    font-family: "Alegreya Sans";
    font-size: 24px;
    line-height: 130%;

    img{
      margin-right: 12px;
    }
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__wrapper-img {
    position: relative;
    img {
      border-radius: 15px;
    }
  }
  &__btns-container {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
  }
  &__btns {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.map__btn {

  width: 50px;
  height: 50px;

  &--prev{
    background-image: url("../img/arrow-left.svg");
    background-repeat:no-repeat;
    background-size: contain;
    background-position: center;
  }

  &--next {
    background-image: url("../img/arrow-right.svg");
    background-repeat:no-repeat;
    background-size: contain;
    background-position: center;
  }
}

.map__swiper-pagination .swiper-pagination-bullet {
  background: transparent;
  border: 2px solid #fff;
  opacity: 1;
  width: 10px;
  height: 10px;
}

.map__swiper-pagination .swiper-pagination-bullet-active {
  background: #fff !important;
  width: 13px !important;
  height: 13px !important;
}


// ДЛЯ СМЕНА СЛАЙДОВ

.map__wrapper-img {
  position: relative;
}
.map__btn-dot {
  position: absolute;
}

// суражская 1а
.map__btn-dot-1 {
  left: 31%;
  top: 84%;
}
// ул. Бобруйская, д,6
.map__btn-dot-2 {
  right: 14%;
  top: 73%;
}
//  ул. Нововиленская, д. 41
.map__btn-dot-3 {
  right: 37%;
  top: 36%;
}
//  ул. Кальварийская, д. 7Б
.map__btn-dot-4 {
  left: 22%;
  top: 35%;
}

.map__slider-item {
  display: none;
}

.map__slider-item.active{
  display: block;
}



@media (max-width: 1010px) {
  .map__wrapper {
    flex-wrap: wrap;
    margin: 50px 0;
    gap: 20px;
  }
  .map__slider {
    width: 100%;
  }

  .map__item-bg {
    min-height: 240px;
    width: 100%;
    border-radius: 15px;
  }

  .map__btns {
    display: none;
  }

  .map__item-bg {
    align-items: start;
    justify-content: end;
  }
  .map__item-content {
    max-width: 260px;
    padding: 15px 16px 26px;
    background-size: contain;
  }
  .map__item-content-text {
    font-size: 20px;
    line-height: 120%;
  }

  .map__wrapper-img {
    width: 100%;
    height: 310px;
  }

    // суражская 1а
  .map__btn-dot-1 {
    left: 36%;
    top: 81%;
  }
  // ул. Бобруйская, д,6
  .map__btn-dot-2 {
    right: 18%;
    top: 65%;
  }
  //  ул. Нововиленская, д. 41
  .map__btn-dot-3 {
    right: 27%;
    top: 6%;
  }
  //  ул. Кальварийская, д. 7Б
  .map__btn-dot-4 {
    left: 25%;
    top: 5%;
  }


}

