.stock {
  padding-top: 50px;

  &__discount {
    border-radius: 17px;
    margin-bottom: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }

  &__discount-main {
    background: url("../img/stock-1.jpg") no-repeat center;
    background-size: cover;
    min-height: 280px;
    .stock__discount-title {
      background: url("../img/blur-stock-1.png") no-repeat center;
      background-size: contain;
      text-align: center;
    }
  }

  &__discount-title {
    font-size: 70px;
    line-height: 120%; /* 84px */
    letter-spacing: -7px;
  }

  &__discount-student {
    background: url("../img/stock-2.jpg") no-repeat center;
    background-size: cover;
    min-height: 470px;
    .stock__discount-content {
      background: url("../img/blur-stock-2.png") no-repeat center;
      background-size: contain;
    }
  }

  &__discount-content {
    text-align: center;
    max-width: 1100px;
    margin-top: 141px;
  }

  &__discount-supertitle {
    font-size: 40px;
    line-height: 110%; /* 44px */
    letter-spacing: -4px;
    margin-bottom: 20px;
    text-transform: uppercase;
  }

  &__discount-ticket {
    background: url("../img/stock-3.jpg") no-repeat center;
    background-size: cover;
    min-height: 470px;
    .stock__discount-content {
      background: url("../img/blur-stock-2.png") no-repeat center;
      background-size: contain;
    }
  }
}


@media (max-width: 1010px) {
  .stock__discount-main {
    min-height: 180px;
  }
  .stock__discount-title {
    font-size: 40px;
    line-height: 110%; /* 44px */
    letter-spacing: -4px;
  }
  .stock__discount {
    margin-bottom: 50px;
  }
  .stock__discount-student {
    min-height: 260px;
  }
  .stock__discount-ticket {
    min-height: 260px;
  }
  .stock__discount-supertitle {
    font-size: 20px;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    text-transform: uppercase;
    margin-bottom: 8px;
  }
  .stock__discount-content {
    max-width: 578px;
    margin-top: 84px;
  }
}

@media (max-width: 600px) {
  .stock {
    padding-top: 40px;
  }
  .stock__discount-main {
    min-height: 80px;
  }
  .stock__discount-title {
    font-size: 20px;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    text-transform: uppercase;
  }
  .stock__discount {
    margin-bottom: 40px;
  }
  .stock__discount-student {
    min-height: 160px;
  }
  .stock__discount-ticket {
    min-height: 160px;
  }
  .stock__discount-supertitle {
    font-size: 12px;
    line-height: 120%; /* 14.4px */
    letter-spacing: -0.6px;
    text-transform: uppercase;
  }
  .stock__discount-content {
    max-width: 350px;
    margin-top: 74px;
  }
}
