.complaint {

  &__wrapper {
    position: relative;
    padding: 50px 0 100px;
  }

  &__main-bg {
    border-radius: 17px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    background: url("../img/complaint-bg.png") no-repeat center;
    background-size: cover;
    min-height: 280px;
  }

  &__main-title {
    text-align: center;
    font-size: 70px;
    line-height: 120%; /* 84px */
    letter-spacing: -7px;
    background: url("../img/complaint-blur.png") no-repeat center;
    background-size: contain;
    padding: 20px;
  }
}

@media (max-width: 1010px) {

  .complaint__main-title {
    font-size: 40px;
    line-height: 110%; /* 44px */
    letter-spacing: -4px;
  }

  .complaint__main-bg {
    min-height: 180px;
  }

  .complaint__wrapper {
    position: relative;
    padding: 50px 0;
  }
}

@media (max-width: 600px) {


  .complaint__main-bg {
    min-height: 80px;
  }
  .complaint__main-title {
    font-size: 18px;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    text-transform: uppercase;
  }
  .complaint__wrapper {
    padding: 40px 0;
  }

}
