.contact {


  &__wrapper {
    position: relative;
    padding: 50px 0 95px;
  }

  &__main-bg {
    border-radius: 17px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    background: url("../img/contact-bg.jpg") no-repeat center;
    background-size: cover;
    min-height: 280px;
  }

  &__main-title {
    text-align: center;
    font-size: 70px;
    line-height: 120%; /* 84px */
    letter-spacing: -7px;
    color: #B14000;
    background: url("../img/contact-blur.png") no-repeat center;
    background-size: contain;
    padding: 20px;
  }

  &__address-item {
    display: flex;
    align-items: center;
    font-family: "Alegreya Sans";
    font-size: 24px;
    font-weight: 700;
    line-height: 130%;

    img {
      margin-right: 12px;
    }
    &:not(:last-child){
      margin-bottom: 24px;
    }
  }

  &__logo {
    position: absolute;
    right: 168px;
    bottom: -21px;
  }
}


@media (max-width: 1010px) {
  .contact__address-item {
    font-size: 20px;
    line-height: 120%;
  }

  .contact__main-title {
    font-size: 40px;
    line-height: 110%; /* 44px */
    letter-spacing: -4px;
  }

  .contact__main-bg {
    min-height: 180px;
  }

  .contact__address-item:not(:last-child) {
    margin-bottom: 10px;
  }
  .contact__wrapper {
    padding: 50px 0 25px;
  }
  .contact__logo img{
    width: 124px;
    height: 171px;
  }
  .contact__logo {
    right: 77px;
    bottom: -14px;;
  }
}

@media (max-width: 600px) {
  .contact__address-item {
    font-size: 16px;
    line-height: 130%;
  }

  .contact__address-item:not(:last-child) {
    margin-bottom: 10px;
  }
  .contact__wrapper {
    padding: 40px 0 25px;
  }
  .contact__logo img{
    width: 124px;
    height: 171px;
  }
  .contact__logo {
    right: 77px;
    bottom: -14px;;
  }
  .contact__main-bg {
    min-height: 80px;
  }
  .contact__main-title {
    font-size: 20px;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    text-transform: uppercase;
  }
}


