
.slider {
  &__main{
    position: relative;
  }
  &__main-wrapper {

  }

  &__main-shadow {
    position: absolute;
    background: rgb(0,0,0);
background: linear-gradient(90deg, rgba(0,0,0,0.8520542005864846) 0%, rgba(146,146,146,0) 28%, rgba(255,255,255,0) 49%, rgba(135,135,135,0) 73%, rgba(0,0,0,1) 100%);
    width: 110%;
    height: 100%;
    margin: 0 20px;
    z-index: 1000;
  }

  &__main {
    position: relative;
    margin-top: 50px;
    margin-bottom: 100px;
  }


  &__item {
    position: relative;
    background-image: url("../img/flat-lay-sushi.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    // padding: 84px 15.53px 87px 7.46px;
    min-height: 420px;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;

  }
  &__flex {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 38px;
  }
  &__item-text {
    max-width: 503px;
    line-height: 140%; /* 22.4px */
    letter-spacing: -0.8px;
    text-transform: uppercase;
    background: url("../img/blur-4.png") no-repeat center;
    background-size: contain;
  }

  &__slider-btn {

    @include bg-cover;
    width: 50px;
    height: 50px;

    &--prev{
      background-image: url("../img/arrow-left.svg");
      margin-left: 20px;
    }

    &--next {
      background-image: url("../img/arrow-right.svg");
      margin-right: 20px;
    }
  }

  &__container {
    width: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 100;
  }

  &__btns {
    display: flex;
    justify-content: space-between;
  }
  &__item-text--mobile {
    display: none;
  }
}

.swiper-pagination-bullet-active {
  background: #2B2D2F !important;
  width: 13px !important;
  height: 13px !important;
}

.swiper-pagination-bullet{
  background: transparent;
  border: 2px solid #2B2D2F;
  opacity: 1;
  width: 10px;
  height: 10px;
}

.swiper-slide {
  box-shadow: 74px 0px 35px 3px rgba(34, 60, 80, 0.24) inset;
}

.slider__scroll {
  position: absolute;
  right: 0.8%;
  bottom: -26px;
  left: auto;
  border-radius: 40px;
  background: rgba(20, 16, 36, 0.08);
  height: 3px;
  width: 200px !important;
}

.swiper-scrollbar-drag {
  background: #D9D9D9;
  border-radius: 0px;
}

.swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 17px;
}

@media (max-width: 1010px) {
  .slider__main {
    padding-left: 10px;
    padding-right: 10px;
    max-width: 768px;
    margin: 50px auto 111px;
  }

  .slider__flex {
    display: block;
  }
  .slider__logo img {
    width: 300px;
    height: 75px;
  }

  .slider__item-text{
    margin-top: 20px;
    max-width: 100%;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%; /* 19.6px */
    letter-spacing: -0.7px;
    background: none;
  }

  .slider__item {
    padding: 0 20px 0;
    min-height: 289px;
  }

  .slider__container {
    width: auto;
    top: auto;
    transform: translateY(0);
    bottom: -68px;
    left: 50%;
    transform: translateX(-50%);
  }
  .slider__btns {
    gap: 10px;
  }
}

@media (max-width: 600px) {
  .slider__item-text {
    display: none;
  }
}


@media (max-width: 400px) {
  .slider__item-text {
    display: none;
  }
  .slider__item-text--mobile {
    display: block;
    text-align: justify;
    margin-top: 14px;
    background: none;
  }
  .slider__logo img {
    width: 160px;
    height: 40px;
  }
  .slider__main {
    padding-left: 10px;
    padding-right: 10px;
    max-width: auto;
    width: 100%;
    margin: 40px auto 40px;
  }
  .slider__item {
    min-height: 90px;
    padding: 20px;
    justify-content: left;
    align-items: start;
  }

  .slider__container {
    width: 100%;
    top: auto;
    bottom: auto;
    top: 10%;

  }
  .slider__slider-btn {
    width: 34px;
    height: 34px;
  }

  .slider__slider-btn--prev {
    margin-left: 0;
  }

  .slider__slider-btn--next {
    margin-right: 0;
  }


}

