.form {
  &__content{
    margin-top: 100px;
  }

  &__wrapper {
    position: relative;
    min-height: 511px;
    // text-align: center;
  }

  &__item {
    max-width: 624px;
    margin: 0 auto;
    border-radius: 15px;
    background: var(--accent-color);
    padding: 60px 82px;
  }

  &__item-title {
    max-width: 270px;
    text-align: center;
    margin: auto;
    margin-bottom: 20px;
    font-size: 16px;
    line-height: 140%; /* 22.4px */
    letter-spacing: -0.8px;
    text-transform: uppercase;
  }

  &__item-border {
    width: 100%;
    height: 2px;
    background: #B14000;
    margin-bottom: 11px;
    border-radius: 20px;
  }

  &__item-wrapper {
    margin-bottom: 30px;
  }

  &__item-label {
    font-family: "Alegreya Sans";
    font-size: 20px;
    line-height: 120%; /* 24px */
    margin-bottom: 20px;
    &.form__item-label:not(:last-child) {
      display: block;
      margin-bottom: 22px;
    }
  }

  &__item-input {
    padding: 5px 20px;
    width: 100%;
    outline:none;
    border: none;
    border-radius: 10px;
    font-family: "Alegreya Sans";
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;
    color: var(--accent-text-color);
  }

  &__item-btn {
    width: 100%;
    font-family: "Alegreya Sans";
    font-size: 20px;
    font-weight: 700;
    line-height: 130%; /* 26px */
    text-transform: uppercase;
    padding: 17px;
    border-radius: 10px;
    background: var(--btn-color);
    border: none;
    outline: none;
    cursor: pointer;
    color: var(--text-color);
  }

  &__item-btn--next {
    text-align: center;
    max-width: 418px;
    margin: 0 auto;
  }

  &__textarea {
    padding: 5px 20px;
    width: 100%;
    height: 152px;
    border-radius: 10px;
    font-family: "Alegreya Sans";
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;
    color: var(--accent-text-color);
    resize: none;
    outline: none;
  }

  &__thanks {
    opacity: 0;
    position: relative;
    margin: auto;
    max-width: 623px;
    min-height: 282px;
    border-radius: 15px;
    background: var(--accent-color);
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    &.active {
      opacity: 1;
      z-index: 1;
    }
  }

  &__thanks-title {
    font-size: 20px;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    text-transform: uppercase;
    margin-bottom: 30px;
  }

  &__thanks-text {
    font-family: "Alegreya Sans";
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%; /* 31.2px */
  }

  &__thanks-btn {
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
  }
  &__item-label--radio {
    position: relative;
    padding-left: 20px;
    font-family: "Alegreya Sans";
    font-size: 24px;
    line-height: 130%;

  }
}

.form__item {
  opacity: 0;
  z-index: -1;
  position: absolute;
  width: 100%;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  &.active {
    opacity: 1;
    z-index: 1;
  }
}

.just-validate-error-label {
  font-family: "Alegreya Sans";
}

// custom radio
.form__item-label--radio input {
  appearance: none;
}
.form__item-label--radio span {
  position: absolute;
  left: 0px;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  flex-shrink: 0;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 1px solid var(--text-color);
}
.form__item-label--radio input:checked + span{
  width: 16px;
  height: 16px;
  background: var(--text-color);
}

.just-validate-error-field {
  border: 2px solid #A01D29;
}

@media (max-width: 1010px) {
  .form__content {
    margin-top: 49px;
  }
}
@media (max-width: 600px){
  .form__content {
    margin-top: 40px;
  }
  .form__item-title {
    font-size: 12px;
    line-height: 120%; /* 14.4px */
    letter-spacing: -0.6px;
    text-transform: uppercase;
  }
  .form__item-label--radio{
    font-size: 16px;
    line-height: 130%;
  }

  .form__item {
    padding: 29px 19px;
  }
  .form__item-title {
    margin-bottom: 10px;
  }
  .form__item-label--radio input:checked + span {
    width: 10px;
    height: 10px;
  }
  .form__item-label--radio span {
    width: 8px;
    height: 8px;
  }
  .form__item-label--radio {
    padding-left: 15px;
  }
  .form__item-label.form__item-label:not(:last-child) {
    margin-bottom: 14px;
  }
  .form__item-wrapper {
    margin-bottom: 14px;
  }
  .form__item-btn {
    padding: 8px;
  }

  .form__wrapper {
    min-height: 336px;
  }
  .just-validate-error-label {
    font-size: 14px;
  }
}


